@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
body {
  height: 100%;
  width: 100%;
  background-color: dodgerblue;
}

.auth_container {
  height: 70vh;
  text-align: center;
  margin: 30px;
}
.submit_container {
  margin: 20vh auto;
  height: 30%;
}

.submit_form {
  height: 65%;
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.twitter_icon {
  margin-bottom: 20px;
  font-size: xx-large;
}

.email_form {
  margin-bottom: 10px;
  height: 40%;
  width: 40%;
  border-radius: 10px;
  background-color: white;
  color: black;
  text-align: center;
}
.password_form {
  margin-bottom: 10px;
  height: 40%;
  width: 40%;
  border-radius: 10px;
  background-color: white;
  color: black;
  text-align: center;
}
.auth_submit_button {
  margin-bottom: 10px;
  height: 40%;
  width: 40%;
  border-radius: 10px;
  font-size: 15px;
}
.auth_btns {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  height: 30%;
  margin: 0 auto;
}
.google_btn {
  margin-bottom: 10px;
  width: 80%;
  height: 20%;
  font-size: 15px;
  border-radius: 15px;
}
.google_icon {
  margin-right: 10px;
}
.github_btn {
  margin-bottom: 10px;
  width: 80%;
  height: 20%;
  font-size: 15px;
  border-radius: 15px;
}
.github_icon {
  margin-right: 10px;
}

